.profile__wrapper {
  display: flex;
}

.back__section {
  width: 64px;
  height: 100vh;
  background-color: #fbfbfb;
}

.back__section:hover {
  cursor: pointer;
}

.profile__container {
  width: 1216px;
  height: 100vh;
  background-color: #fff;
}

.user__container {
  height: 80vh;
  flex-direction: column;
  justify-content: space-between;
  padding-top: 50px;
  display: flex;
}

.avatar__img {
  width: 150px;
  height: 150px;
  background-image: url("../../no-avatar.c5798d35.png");
  background-repeat: no-repeat;
  background-size: cover;
}

.avatar__icon {
  margin: 0;
  display: block;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.user-data__container {
  width: 800px;
  flex-direction: column;
  justify-content: end;
  align-items: flex-end;
  display: flex;
}

.user-data__container input {
  width: 500px;
}

.user-data__container input::placeholder {
  text-align: right;
}

.user-data__container div {
  width: 700px;
}

.change__container {
  flex-direction: column;
  display: flex;
}

.change__container .logout {
  color: red;
}

.change__container a {
  margin: 10px auto;
}

.save__edit {
  width: 280px;
  height: 37px;
  color: #fff;
  background: #3369f3;
  border-radius: 8px;
}

.save__edit:hover {
  cursor: pointer;
}

